// base spacing

$base-space: 30px;


@mixin flex {
  @media #{$md} {
  display:flex;
  align-items:center;
  }
}

//base container
@mixin container {
  // to determine distnace from sides with screen sizes
  margin:0 auto;
  position:relative;
  @include clearfix;
  clear:both;
  @include padding(0, null);
  width:100%;
  padding-left:$base-space / 2;
  padding-right:$base-space / 2;
  @media #{$xs} {
    max-width:$small;
  }
  @media #{$sm} {
    max-width:$small - $base-space;
  }
  @media #{$md} {
    max-width:$medium - $base-space;
  }
  @media #{$lg} {
    max-width:$large - $base-space;
  }
  @media #{$xl} {
    max-width:$xlarge - $base-space;
  }
  @media #{$xxl} {
    max-width:$xxlarge - $base-space;
  }
}

@mixin container_nopad {
  @include container;
  padding-left:0;
  padding-right:0;
}

.container-nopad {
    @include container_nopad;
}

@mixin container_fluid {
  // to determine distnace from sides with screen sizes
  $base-space: 30px;
  margin:0 auto;
  @include clearfix;
  clear:both;
  @include padding(0, null);
  width:100%;
  padding-left:$base-space / 2;
  padding-right:$base-space / 2;
  @media #{$xs} {
    max-width:$xlarge;
  }
}

// container widths function

@function operator($width, $p) {
  @if $width == 1 {
    @if($p == 'neg') {
     @return calc(-100%/12);
   } @else {
     @return calc(100%/12);
   }
  } @else if $width == 2 {
    @if($p == 'neg') {
     @return calc(-100%/6);
     } @else {
     @return calc(100%/6);
   }
  } @else if $width == 3 {
    @if($p == 'neg') {
     @return calc(-100%/4);
   } @else {
     @return calc(100%/4);
   }
  } @else if $width == 4 {
    @if($p == 'neg') {
     @return calc(-100%/3);
   } @else {
     @return calc(100%/3);
   }
  } @else if $width == 5 {
    @if($p == 'neg') {
     @return calc(-100%/2.4);
   } @else {
     @return calc(100%/2.4);
   }
  } @else if $width == 6 {
    @if($p == 'neg') {
     @return -50%;
   } @else {
     @return 50%;
   }
  } @else if $width == 7 {
    @if($p == 'neg') {
     @return calc(-100% / 12 + #{$half});
   } @else {
     @return calc(100% / 12 + #{$half});
   }
  } @else if $width == 8 {
    @if($p == 'neg') {
     @return calc(-100% / 6 + #{$half});
   } @else {
     @return calc(100% / 6 + #{$half});
   }
  } @else if $width == 9 {
    @if($p == 'neg') {
     @return calc(-100% / 4 + #{$half});
   } @else {
     @return calc(100% / 4 + #{$half});
   }
  } @else if $width == 10 {
    @if($p == 'neg') {
     @return calc(-100% / 3 + #{$half});
   } @else {
     @return calc(100% / 3 + #{$half});
   }
  } @else if $width == 11 {
    @if($p == 'neg') {
     @return calc(100% / 2.4 + #{$half});
   } @else {
     @return calc(-100% / 2.4 + #{$half});
   }
  } @else if $width == 12 {
    @if($p == 'neg') {
     @return -100%;
   } @else {
     @return 100%;
   }
  }
}

$half: 50%;
// column widths
@mixin col($width, $p: null) {
  @media #{$md} {
  float:left;
  padding-top:0;
  padding-bottom:0;
    width: operator($width, $p: null);
  }
  padding:$base-space / 2;
}

// push columns
@mixin push($width, $p: null) {
  @media #{$sm} {
    margin-left: operator($width, $p: null);
  }
}

//pull columns
@mixin pull($width, $p: 'neg') {
  @media #{$sm} {
    $result: operator($width, $p: 'neg');
    margin-left:$result;
  }
}

//row to compensate for container padding
@mixin row {
  @include clearfix;
  @media #{$sm} {
    margin-left: -$base-space / 2;
    margin-right: -$base-space / 2;
  }
}
// use class too
.row {
  @include row;
}
