#about {
  .entry-header {
    display:none;
  }
  .container {
    @include large_pad;
    @include min-width;
    @include flex;
    > div:only-child {
      width:100%;
      max-width:$medium;
      margin:0 auto;
    }
    h2 {
      font-size:2em;
    }
    strong {
      color:$darkblue;
    }
  }
 .site-content,
 .sidebar {
   @include col(6);
   padding-bottom:$base;
   @media #{$md} {
     &.sidebar {
       padding-right:5%;
     }
   }
   img {
     @media #{$md} {
       float:left;
    }
    margin:0 auto;
    display:block;
   }
 }
 .scroll-down {
   position:relative;
   color:$darkblue;
   transform:none;
   padding-left:0;
   padding-right:0;
   left:0;
   top:0;
   bottom:0;
   right:0;
 }
}
