.site-header {
  @include small_pad;
  position:absolute;
  left:0;
  right:0;
  text-align:center;
}

.custom-logo-link {
  transition:opacity $fast;
  position:relative;
  z-index:99;
  display:inline-block;
  display:none;
  @at-root .site-header {
    & {
        margin-top:2%;
    }
  }
  img {
    max-width:150px;
    width:100%;
    @media #{$xs-down} {
      max-width:90px;
    }
  }
  &:hover {
    opacity: $opac;
  }
}

.screen-reader-text {
  display:none;
}

.site-footer {
  @include large_pad;
  background:$blue;
  .container-nopad {
    p {
      font-weight:300;
    }
    > .placeholder-text {
      text-align:center;
    }
    .icon {
      font-size:2.5rem;
    }
    color:white;
    > div {
      &:first-child {
        *:first-of-type {
          margin:0;
          margin-top:5px;
        }
        @media #{$xl} {
          text-align:left;
        }
      }
      margin:0 auto;
      text-align:center;
      margin:2em 0;
      &:last-child {
        ul {
          display:inline-block;
          text-align:center;
          padding:0;
          li {
            float:left;
            &:not(:last-child) {
              margin-right:$base / 2;
            }
            list-style-type:none;
            a {
              font-size:2.5em;
            }
          }
        }
      @media #{$xl-down} {
          margin-bottom:0;
        }
      }
    }
    a {
      font-weight:300;
      display:inline-block;
      clear:both;
      color:white;
      text-align:center;
      &.custom-logo-link {
        display:block;
        + a {
          @media #{$xl} {
            float:left;
          }
        }
      }
      &:hover {
        color:$rgbblue;
      }
    }
    padding-left:$base / 2;
    padding-right:$base / 2;
    @media #{$xl} {
      br {
        display:none;
      }
    @include flex;
    a {
      text-align:left;
    }
    form {
      //max-width:100%;
      button {
        width:40%;
      }
      input {
        width:60%;
      }
    }
    > div {
    display:block;
    @include col(3);
    &:nth-child(2) {
      @include col(6);
      max-width:50%;
    }
    &:last-child {
      a {
          text-align:center;
          }
        }
      }
    }
  }
}
.sub-footer {
  text-transform:uppercase;
  letter-spacing:1px;
  @media #{$xl-down} {
    text-align:center;
  }
  background:$rgbblue;
  color:$blue;
  padding-top:$base / 3;
  padding-bottom: $base / 3;
  p {
    margin:0;
    @include col(12);
    font-size:14px;
  }
}

.placeholder-logo {
  z-index:99999;
}

.custom-logo-link + .custom-logo-link {
  display:none !important;
}
