// Mixins

// Padding for sections
@mixin large_pad {
  $pad: 150px;
  @media #{$sm} {
  padding-top:$pad;
  padding-bottom:$pad;
  }
  padding-top:$pad / 2;
  padding-bottom:$pad / 2;
}

@mixin small_pad {
    $pad: 50px;
    @media #{$sm} {
      padding-top:$pad;
      padding-bottom:$pad;
  }
  padding-top:$pad / 2;
  padding-bottom:$pad / 2;
}


@mixin med_pad {
    $pad: 100px;
    @media #{$sm} {
      padding-top:$pad;
      padding-bottom:$pad;
  }
  padding-top:$pad / 2;
  padding-bottom:$pad / 2;
}

// Convert to rem
@function calculateRem($size) {
  $remSize: $size / 16px;
  @return #{$remSize}rem;
}

@mixin fontSize($size) {
  font-size: $size; //Fallback in px
  font-size: calculateRem($size);
}

// optional smaller width content area
@mixin min-width {
  @include clearfix;
  @media #{$lg} {
    max-width:$large;
    margin:0 auto;
  }
}

// vertical alignment
@mixin vert_align {
  position:absolute;
  top:50%;
  transform:translateY(-50%);
  left:0;
  right:0;
}

// text line
@mixin text_line {
  position:relative;
  display:inline-block;
  &:before,
  &:after {
    content:'';
    height:1px;
    top:50%;
    width:1000px;
    background:#ccc;
    position:absolute;
  }
  &:before {
    right:110%;
  }
  &:after {
    left:110%;
  }
}

// clearfix
@mixin clearfix {
  &:after {
    content:'';
    display:block;
    clear:both;
  }
}

// basics padding mixin
@mixin padding($left, $right) {
  padding-left:$left;
  padding-top:$right;
  padding-right:$left;
  padding-bottom:$right;
}

/// invert
@mixin invert {
  filter:invert(100%);
  -webkit-filter:invert(100%);
  -moz-filter:invert(100%);
  -ms-filter:invert(100%);
  -o-filter:invert(100%);
}

// Remove all button attrs
@mixin strip {
  appearance:none;
  -webkit-appearance:none;
  -moz-appearance:none;
  -o-appearance:none;
  -ms-appearance:none;
  background:none;
  border:none;
  border-radius:none;
  box-shadow:none;
  outline:none;
  text-shadow:none;
}

@mixin input_margin {
  margin-bottom:10px;
  margin-right:5px;
  margin-top:10px;
}

@mixin strip_border {
  appearance:none;
  -webkit-appearance:none;
  -moz-appearance:none;
  -o-appearance:none;
  -ms-appearance:none;
  background:none;
  border-radius:0 !important;
  box-shadow:none;
  outline:none;
  text-shadow:none;
  &:hover {
    background:none !important;
    box-shadow:none;
  }
}

// Headings Mixin
@mixin headings {
    h1, h2, h3,
    h4, h5, h6 {
        @content;
    }
}

/// bg cover
@mixin cover {
  background-size:cover !important;
  background-position:center center !important;
  background-repeat:no-repeat;
}

// text shadow
@mixin text_shadow {
  text-shadow:0px 0px 7px rgba(0,0,0,.55);
}


// IOS query

// IOS meid

@mixin iOS {
  @media	only screen and (-webkit-min-device-pixel-ratio: 1.3),
	only screen and (-o-min-device-pixel-ratio: 13/10),
	only screen and (min-resolution: 120dpi)
	{
		@content;
	}

}

// Bottom Fade
@mixin bottom_fade {
  content:'';
  position:absolute;
  bottom:0;
  right:0;
  top:auto;
  left:0;
  height:150px;
  opacity:.3;
  z-index:999;
  /* Permalink - use to edit and share this gradient: http://colorzilla.com/gradient-editor/#000000+0,000000+100&0+0,0+0,0.85+99 */
  background: -moz-linear-gradient(top, rgba(0,0,0,0) 0%, rgba(0,0,0,0.85) 99%, rgba(0,0,0,0.85) 100%); /* FF3.6-15 */
  background: -webkit-linear-gradient(top, rgba(0,0,0,0) 0%,rgba(0,0,0,0.85) 99%,rgba(0,0,0,0.85) 100%); /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(to bottom, rgba(0,0,0,0) 0%,rgba(0,0,0,0.85) 99%,rgba(0,0,0,0.85) 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#00000000', endColorstr='#d9000000',GradientType=0 ); /* IE6-9 */
}

// Placeholder
@mixin placeholder {
  &::-webkit-input-placeholder {@content}
  &:-moz-placeholder           {@content}
  &::-moz-placeholder          {@content}
  &:-ms-input-placeholder      {@content}
}
