#features {
  background: lighten($lightblue, 2%);
  @include small_pad;
  @media #{$lg} {
    @include med_pad;
  }
  .container {
    max-width:$xlarge;
    .feature-content > *:first-child:not(p) {
      @include fontSize($hbase * 1.1);
      border-bottom:1px solid rgba($blue, .5);
      padding-bottom:.72em;
      display:inline-block;
      width:auto;
      margin-bottom:0;
      font-weight:300;
      color:$text-color;
      strong {
        font-weight:900;
        color:$darkblue;
      }
    }
    .feature {
      > div {
        position:relative;
        top:-20px;
        opacity:0;
        transition:opacity $superslow cubic-bezier(0.230, 1.000, 0.320, 1.000), top $superslow cubic-bezier(0.230, 1.000, 0.320, 1.000);
      }
      a {
        &:hover {
          color:$blue;
        }
      }
      .feature-image {
        @include clearfix;
        @media #{$md-down} {
          margin-bottom:1em;
        }
        img {
          max-height:400px;
          width:auto;
          margin:0 auto;
          display:block;
        }
      }

      @include small_pad;

      p {
        //max-width: 450px;
      }
      @include flex;

      &:nth-child(even) {
        div img {
          float: left;
        }
      }
      &:nth-child(odd) {
        div img {
          float: right;
        }
      }

      &:nth-child(odd) {
        .feature-image {
          order: 2;
        }
      }

      > div:first-child {
        @include col(6);
      }
      > div:last-child {
        @include col(6);
      }
    }

      img {
        width:100%;
        @media #{$sm-down} {
          float: none !important;
          display: block;
          margin: 0 auto;
      }
    }
  }
}


// feature fadeIn

.fade-in {
  opacity:1 !important;
  top:0 !important;
}
