@import url('https://fonts.googleapis.com/css?family=Open+Sans:300,400,,600,700,900');
@import url('http://code.ionicframework.com/ionicons/2.0.1/css/ionicons.min.css');
body,
html {
  line-height:$base-line-height;
  font-family:'Open Sans';
  font-size:$base-font-size;
  color:$text-color;
  font-weight:300;
  @media #{$sm-down} {
    font-size: $base-font-size - 1px;
  }
}

@include headings {
  display:inline-block;
  width:100%;
  font-weight:300;
  line-height:$base-line-height - .3;
  margin-bottom:.5em;
  margin-top:$base / 2;
  margin-top:0;
}

a {
  text-decoration:none;
}


h6 {
  @include fontSize($hbase * 1.1);
}
h5 {
  @include fontSize($hbase * 1.3);
}
h4 {
  @include fontSize($hbase * 1.5);
}
h3 {
  @include fontSize($hbase * 1.95);
}
h2 {
  @include fontSize($hbase * 2.35);
}
h1 {
  @include fontSize($hbase * 3);
  margin-bottom:.4em;
}
