@import 'site-header';
@import 'sidebar';

#content {
  .content-wrapper {
  }
  @include container_nopad;
    @include large_pad;
  .content-area {
    @include col(8);
  }
  .widget-area {
    @include col(4);
  }
}

#content {
  display:none;
}

.full-height {
  height:100vh;
}

body {
  transition:opacity .3s;
}

.page-load {
  opacity:.5;
}

.body-load:before {
  content::'';
  height:100px;
  width:100px;
  background:url('assets/img/loader.gif');
  background-size:100%;
  position:fixed;
  top:50%;
  transform:translateY(-50%);
  color:white;
  left:0;
  right:0;
  bottom:0;
  margin:0 auto;
  display:block;
  text-align:center;
  z-index:9999;
}


