//screen sizes
$extra-small: 480px;
$small: 620px;
$medium: 768px;
$large: 992px;
$xlarge: 1180px;
$xxlarge: 1400px;

// media queries
$xs: 'only screen and (min-width:#{$extra-small})';
$xs-down: 'only screen and (max-width:#{$extra-small - 1px})';
$sm: 'only screen and (min-width:#{$small})';
$sm-down: 'only screen and (max-width:#{$small - 1px})';
$md: 'only screen and (min-width:#{$medium})';
$md-down: 'only screen and (max-width:#{$medium - 1px})';
$lg: 'only screen and (min-width:#{$large})';
$lg-down: 'only screen and (max-width:#{$large - 1px})';
$xl: 'only screen and (min-width:#{$xlarge})';
$xl-down: 'only screen and (max-width:#{$xlarge - 1px})';
$xxl: 'only screen and (min-width:#{$xxlarge})';
$xxl-down: 'only screen and (max-width:#{$xxlarge - 1px})';

@import 'grid_mixins';
