// base spacing for elements (other than grid)
$base: 40px;

//transitions
$fast: .2s;
$slow: .4s;
$superslow: 1.5s;

$opac: .7;

// type
$base-font-size: 18px;
$hbase: $base-font-size * 1.2;
$base-line-height:1.4;

// colors

$text-color:#444;
$blue: #4EBDFF;
$darkblue: $blue;
$rgbblue:#0000ff;
$lightblue:#e6f1f8;
