//@import 'reset';
@import 'vars';
@import 'mixins';
// custom pete grid
@import 'grid/grid';
@import 'type';

// other base things


* {
    -moz-box-sizing: border-box;
    -webkit-box-sizing: border-box;
     box-sizing: border-box;
}

.container {
  @include container;
}

img {
  max-width:100%;
  height:auto;
}

a,
button,
input[type=submit] {
  cursor:pointer;
}

a {
  transition:color $fast;
}

.hidden {
  display:none;
}

form {
  display:block;
  border-bottom:1px solid white;
  display:inline-block;
  width:100%;
  max-width:475px;
  input,
  button {
    &:focus,
    &:active {
      outline:none;
    }
    width:auto;
    padding:15px 5px;
    font-size:$base-font-size;
    border:none;
    font-weight:300;
    background:none;
    color:white;
    float:left;
    width:75%;
    @include placeholder {
      color:white;
      opacity:.6;
      letter-spacing:3px;
      text-align:left;
      transition:opacity $fast;
    }
    &:focus,
    &:hover {
      @include placeholder {
        opacity:.35;
      }
    }
    &:not(button) {
      cursor:text;
    }
  }
  button[type=submit] {
    cursor:pointer;
    transition: opacity $fast;
    letter-spacing:2px;
    width:25%;
    float:right;
    border:none;
    text-align:right;
    text-transform:uppercase;
    &:before {
      font-size:.8rem;
      transition:margin $fast;
      margin-right:10px;
      //content:'\f125';
      content:'\f109';
      font-family:ionicons;
      position:relative;
      bottom:1px;
    }
    &:hover {
      opacity: $opac;
      &:before {
        margin-right:5px;
      }
    }
  }
  @media #{$sm-down} {
  //  border:none;
    input {
      width:90%;
    //  border-bottom:1px solid white;
      display:block;
      //width:100%;
    }
    button[type=submit] {
      width:10%;
      padding-bottom:0;
      &:before {
        font-size:1.25rem;
        left:10px;
        top:0;
      }
      span {
        display:none;
      }
    }
  }
}

#tinymce {
  padding:20px !important;
}
