#hero {
 overflow:hidden;
 position:relative;
 @extend .full-height;
 @media #{$md} {
   min-height:$medium;
 }
 min-height:$small;
 color:white;
 background-color:$blue;
 .hero-bg {
   will-change: transform;
   position:absolute;
   top:0;
   bottom:0;
   left:0;
   right:0;
   //background-blend-mode:color-burn;
   //background-color:$blue;
   background-position:center center;
  // @include cover;
   opacity:.5;
   //background-size:400px;
 }
 .container {
   .content {
      max-width:$medium;
      margin:0 auto;
   }
   p {
     font-weight:300;
   }
   form {
     display:block;
     margin:0 auto;
     @include clearfix;
   }
   @include vert_align;
   top:50%;
   @media #{$sm-down} {
     top:45%;
   }
    text-align:center;
    h2 {
      max-width:$medium - 150px;
      text-transform:none;
      font-size:166%;
      margin-bottom:1.5em;
      @media #{$md-down} {
        max-width:480px;
        margin-bottom:20px;
        font-size:120%;
      }
    }
    h1 {
      line-height:.9;
      + img {
        max-width:80%;
      }
      &:first-line {
        color:white;
        font-size:70%;
        font-weight:400;
      }
      color:$rgbblue;
      text-shadow:0px 0px 15px rgba(0,0,0,.2);
      font-size:100px;
      margin-bottom:.4em;
      font-weight:800;
      text-transform:uppercase;
      @media #{$md-down} {
          font-size:70px;
          max-width:550px;
      }
      @media #{$xs-down} {
        font-size:35px;
        font-size:13vw;
      }
    }
 }
 .svg {
   height: .55em;
   &:nth-child(2) {
     height:.8em;
   }
   @media #{$lg} {
   position:relative;
    top:.015em;
  }
   margin: 3px 3px -2px 5px;
   @include iOS {
     position:relative;
     bottom:2px;
     margin-left:2px;
   }
 }
}

path {
    fill: orange !important;
}

.scroll-down {
  transition: opacity $fast;
  text-transform:uppercase;
  letter-spacing:2px;
  position:absolute;
  @media #{$md} {
  bottom:$base * 1.5;
  }
  bottom:$base * 1.5;
  left:auto;
  right:50%;
  transform:translateX(50%);
  margin:0 auto;
  display:inline-block;
  font-weight:300;
  color:white;
  padding:20px;
  &:after {
    transition:top $fast;
    content:'\f103';
    position:absolute;
    top:75%;
    left:0;
    right:0;
    font-family:ionicons;
    text-align:center;
  }
  &:hover {
    opacity: $opac;
    &:after {
      top:85%;
    }
  }
}

.themeDir {
  display:none;
}


.signup-result {
  display:none;
  position:fixed;
  top:0;
  left:0;
  right:0;
  bottom:0;
  width:100%;
  background:rgba($blue, .9);
  z-index:999999;
  padding:30px;
  margin:0 auto;
  text-align:center;
  color:white;
  p {
    font-size:28px;
    font-weight:300;
    margin:0;
    position:absolute;
    left:$base / 2;
    right:$base / 2;
    top:50%;
    transform:translateY(-50%);
  }
}
